import Vue from 'vue'
import Router from 'vue-router'
// home
import home from '@/page/home/home.vue'
// person
import person from '@/page/person/person.vue'
// 
import containner from '@/page/containner/containner'
//
import search from '@/page/search/search'
//
import login from '@/page/login/login'
// 
import loginend from '@/page/login/components/loginend.vue'
// 
import regist from '@/page/login/components/regist.vue'
//
import collection from '@/page/collect/collect.vue'
Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'home',
            component: home,
            meta: {
                keepAlive: true //
            }
        }, {
            path: '/Person',
            name: 'person',
            component: person
        },
        // 
        {
            path: '/Containner/:id',
            name: 'containner',
            component: containner
        },
        {
            path: '/Search',
            name: 'search',
            component: search,
            meta: {
                keepAlive: true //
            }
        },
        {
            // 
            path: '/Login',
            name: 'login',
            component: login,
            //  tab
            redirect: "/Loginend",
            children: [{
                    //  
                    path: '/Loginend',
                    name: 'loginend',
                    component: loginend,
                },
                {
                    //  
                    path: '/Regist',
                    name: 'regist',
                    component: regist,
                },
            ]
        },
        // 
        {
            path: '/Collection/',
            name: 'collection',
            component: collection
        },

    ]
})


