<template>
  <div>
    <form class="submit">
      <input type="text" v-model="phoneData" placeholder="" />
      <input type="password" v-model="passwordData" placeholder="" />
      <input
        type="button"
        :disabled="!isregister"
        :class="{'regid_activeOne':sendGrides,regid_activeTwo:finishi}"
        v-bind="loginIng"
        value=""
        @click="loGin"
      />
    </form>
  </div>
</template>
<script>
import { login } from "../../../api/api";
import cookie from '../../../axios/cookie'
export default {
  name: "Loginend",
  data() {
    return {
      phoneData: "",
      passwordData: "",
      isregister: false,
      sendGrides: true,
      finishi: false,
      iphones: ""
    };
  },
  computed: {
    // data, 
    //data
    loginIng() {
      if (this.phoneData && this.passwordData) {
        this.isregister = true;
        this.finishi = true;
      } else {
        this.isregister = false;
        this.finishi = false;
      }
    }
  },
  methods: {
    // 
    showToast() {
      this.$popus(this.iphones);
    },
    // 
    loGin() {
      var that = this;
      login({
        username: that.phoneData,
        password: that.passwordData
      })
        .then(res => {
          console.log(res);
          cookie.setCookie("name", that.phoneData, 7);
          cookie.setCookie("token", res.data.token, 7);
          //
          that.$store.dispatch("users");
          //  this.$router.push({ name: "home" }); 
          this.$router.push("/");
        })
        .catch(err => {
          // console.log(err.non_field_errors[0])
          if (err.non_field_errors[0]) {
            that.iphones = "";
            that.showToast(that.iphones);
          }
        });
    }
  }
};
</script>
<style scoped>
.submit {
  padding: 0 0.8rem;
}
input {
  width: 100%;
  padding: 0.2rem 0;
  margin: 0.2rem 0;
}
input[type="button"] {
  font-size: 0.27rem;
  color: #fff;
  border-radius: 0.5rem;
  margin-top: 0.6rem;
}
input:not([type="button"]) {
  border: 1px solid#5858FA;
}
input:nth-child(1) {
  margin-top: 1rem;
}
input:nth-child(1):hover {
  border: 1px solid#FE642E;
}
input:nth-child(2):hover {
  border: 1px solid#82FA58;
}
.regid_activeOne {
  background: #b45837;
}
.regid_activeTwo {
  background: #f5602a;
}
</style>


