<template>
  <div class="collect-wrap">
    <div class="collect" v-if="isShow" @click="toCollect">
      <img src="../../../assets/image/weishoucang.png" alt />
      <p></p>
    </div>
    <div class="collect" v-else @click="toCancelCollect">
      <img src="../../../assets/image/yishou.png" alt />
      <p></p>
    </div>
  </div>
</template>
<script>
import cookie from "../../../axios/cookie.js";
import { getcoll, collect, delectCollect } from "../../../api/api";
export default {
  name: "collect",
  props: {
    productId: ""
  },
  data() {
    return {
      isShow: true
    };
  },
  created() {
    var productId = this.productId;
    // 
    if (cookie.getCookie("token")) {
      // 
      getcoll(productId)
        .then(res => {
          console.log(":", res);
          // 
          this.isShow = false;
        })
        .catch(err => {
          console.log(err);
          this.isShow = true;
        });
    } else {
      this.isShow = true;
    }
  },
  methods: {
    // 
    toCollect() {
      if (cookie.getCookie("token")) {
        collect({
          goods: this.productId
        })
          .then(res => {
            console.log(res);
            console.log("");
            this.isShow = false;
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        this.$popus("2");
        setTimeout(res => {
          this.$router.push("/Person");
        }, 2000);
      }
    }, // 
    toCancelCollect() {
      if (cookie.getCookie("token")) {
        delectCollect(this.productId)
          .then(res => {
            console.log(res);
            console.log("");
            this.isShow = true;
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        this.$popus("2");
        setTimeout(res => {
          this.$router.push("/Person");
        }, 2000);
      }
    }
  }
};
</script>
<style scoped>
.collect-wrap {
  display: flex;
  justify-content: flex-end;
  padding: 0.3rem 0.1rem 0.1rem 0;
}
.collect {
  text-align: center;
}
.collect img {
  width: 0.45rem;
}
.collect p {
  line-height: 0.5rem;
  font-size: 0.25rem;
}
</style>


